<!---->
<template>
  <div class="progress-bar" ref="progressBar" @click="progressClick">
    <div class="bar-inner">
      <div class="progress" ref="progress"></div>
      <div class="progress-btn-wrapper" ref="progressBtnWrapper"
           @touchstart.prevent="onProgressTouchStart"
           @touchmove.prevent="onProgressTouchMove"
           @touchend="onProgressTouchEnd">
        <div class="progress-btn"></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {prefixStyle} from 'common/js/dom'

  const transform = prefixStyle('transform')
  const progressBtnWidth = 16 // 
  export default {
    props: {
      percent: { // 
        type: Number,
        default: 0
      }
    },
    created() {
      this.touch = {} // 
    },
    methods: {
      // 
      onProgressTouchStart(e) {
        this.touch.initiated = true // 
        this.touch.startX = e.touches[0].pageX // touches,
        this.touch.left = this.$refs.progress.clientWidth // 
      },
      // 
      onProgressTouchMove(e) {
        if (!this.touch.initiated) { // 
          return
        }
        const deltaX = e.touches[0].pageX - this.touch.startX // 
        const offsetWidth = Math.min(this.$refs.progressBar.clientWidth - progressBtnWidth, Math.max(0, this.touch.left + deltaX))
        this._offset(offsetWidth)
      },
      // 
      onProgressTouchEnd() {
        this.touch.initiated = false
        this._triggerPercent()
      },
      _offset(offsetWidth) {
        this.$refs.progress.style.width = `${offsetWidth}px`
        this.$refs.progressBtnWrapper.style[transform] = `translate3d(${offsetWidth}px, 0, 0)` // 
      },
      // 
      _triggerPercent() {
        const progressBarWidth = this.$refs.progressBar.clientWidth - progressBtnWidth
        const percent = this.$refs.progress.clientWidth / progressBarWidth
        this.$emit('percentChange', percent) // playerpercentChange
      },
      // 
      progressClick(e) {
        const rect = this.$refs.progressBar.getBoundingClientRect()
        const offsetWidth = e.pageX - rect.left
        this._offset(offsetWidth) // 
        this._triggerPercent()
      }
    },
    watch: {
      percent(newPercent) {
        if (newPercent >= 0 && !this.touch.initiated) {
          const progressBarWidth = this.$refs.progressBar.clientWidth - progressBtnWidth //  - 
          const offsetWidth = newPercent * progressBarWidth
          this.$refs.progress.style.width = `${offsetWidth}px`
          this.$refs.progressBtnWrapper.style[transform] = `translate3d(${offsetWidth}px, 0, 0)` // 
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .progress-bar
    height: 30px
    .bar-inner
      position: relative
      top: 13px
      height: 3px
      background: rgba(0, 0, 0, 0.3)
      .progress
        position: absolute
        height: 100%
        background: $color-theme
      .progress-btn-wrapper
        position: absolute
        left: -8px
        top: -13px
        width: 30px
        height: 30px
        .progress-btn
          position: relative
          top: 7px
          left: 7px
          box-sizing: border-box
          width: 16px
          height: 16px
          border: 3px solid $color-text
          border-radius: 50%
          background: $color-theme
</style>



