<template>
  <div class="singer" ref="singer" >
    <list-view :data="singers" @select="selectSinger" ref="listView">
    </list-view>
    <!---->
    <router-view>
    </router-view>
  </div>
</template>

<script>
  import {GetSingerList} from 'api/singer'
  import {ERR_OK} from 'api/config'
  import Singer from 'common/js/singer'
  import ListView from 'base/listview/listview'
  import {mapMutations} from 'vuex'
  import {playlistMixin} from 'common/js/mixin'

  const HOST_NAME = ''
  const HOST_SINGER_LEN = 10

  export default {
    mixins: [playlistMixin],
    data() {
      return {
        singers: []
      }
    },
    created() {
      this._getSingerList()
    },
    methods: {
      // minbug
      handlePlaylist(playlist) {
        const Bottom = playlist.length > 0 ? '60px' : ''
        this.$refs.singer.style.bottom = Bottom
        this.$refs.listView.refresh()
      },
      _getSingerList() {
        GetSingerList().then((res) => {
          if (res.code === ERR_OK) {
            this.singers = this._normalizeSinger(res.data.list)
          }
        })
      },
      // 
      selectSinger(singer) {
        this.$router.push({
          path: `/singer/${singer.id}`
        })
        this.setSinger(singer) // vuex
      },
      //  list
      _normalizeSinger(list) {
        let map = {
          hot: {
            title: HOST_NAME, // 
            items: [] // 
          }
        }
        // 
        list.forEach((item, index) => {
          if (index < HOST_SINGER_LEN) {
            map.hot.items.push(new Singer({ // class
              id: item.Fsinger_mid, // id
              name: item.Fsinger_name // 
            }))
          }
          // 
          const key = item.Findex // 
          if (!map[key]) {
            map[key] = {
              title: key,
              items: []
            }
          }
          map[key].items.push(new Singer({
            id: item.Fsinger_mid, // Fsinger_mid: "002J4UUk29y8BY"
            name: item.Fsinger_name // 
          }))
        })
        // map
        let hot = [] // 
        let ret = [] // 
        for (let key in map) {
          let val = map[key]
          if (val.title.match(/[a-zA-Z]/)) {
            ret.push(val)
          } else if (val.title === HOST_NAME) {
            hot.push(val)
          }
        }
        ret.sort((a, b) => { // A-Z
          return a.title.charCodeAt(0) - b.title.charCodeAt(0)
        })
        return hot.concat(ret)
      },
      ...mapMutations({
        setSinger: 'SET_SINGER'
      })
    },
    components: {
      ListView
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .singer
    position: fixed
    top: 88px
    bottom: 0
    width: 100%
</style>



